.dotnav {
  padding: 0;
  margin: 0;
  text-align: center;
  list-style: none;
}
.dotnav > li {
  position: relative;
  display: inline-block;
  width: 16px;
  height: 16px;
  margin: 0 16px;
  vertical-align: top;
  list-style: none;
}
.dotnav > li > a {
  display: inline-block;
  width: 100%;
  height: 100%;
  overflow: hidden;
  text-indent: 100%;
  white-space: nowrap;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0.3);
  border-radius: 50%;
}
.dotnav > li > a:focus {
  outline: none;
}
.dotnav > li.active > a,
.dotnav > li:hover > a,
.dotnav > li:focus > a {
  background-color: #ffffff;
}
.dotnav-scaleup > li > a {
  -webkit-transition: transform 0.3s ease 0s, background-color 0.3s ease 0s;
  -o-transition: transform 0.3s ease 0s, background-color 0.3s ease 0s;
  transition: transform 0.3s ease 0s, background-color 0.3s ease 0s;
}
.dotnav-scaleup > li.active > a {
  -webkit-transform: scale(1.5);
  -ms-transform: scale(1.5);
  -o-transform: scale(1.5);
  transform: scale(1.5);
}
.dotnav-stroke > li > a {
  border: 2px solid transparent;
  -webkit-transition: border 0.3s ease 0s, background-color 0.3s ease 0s;
  -o-transition: border 0.3s ease 0s, background-color 0.3s ease 0s;
  transition: border 0.3s ease 0s, background-color 0.3s ease 0s;
}
.dotnav-stroke > li.active > a {
  background-color: transparent;
  border-color: #ffffff;
  -webkit-transform: scale(1.3);
  -ms-transform: scale(1.3);
  -o-transform: scale(1.3);
  transform: scale(1.3);
}
.dotnav-fillin > li > a {
  background-color: transparent;
  box-shadow: 0 0 0 2px #fff inset;
  -webkit-transition: box-shadow 0.3s ease 0s;
  -o-transition: box-shadow 0.3s ease 0s;
  transition: box-shadow 0.3s ease 0s;
}
.dotnav-fillin > li:hover > a,
.dotnav-fillin > li:focus > a {
  background-color: transparent;
  box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.6) inset;
}
.dotnav-fillin > li.active > a {
  box-shadow: 0 0 0 8px #fff inset;
}
.dotnav-dotstroke > li > a {
  box-shadow: 0 0 0 8px rgba(255, 255, 255, 0.5) inset;
  -webkit-transition: box-shadow 0.3s ease 0s;
  -o-transition: box-shadow 0.3s ease 0s;
  transition: box-shadow 0.3s ease 0s;
}
.dotnav-dotstroke > li.active > a {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 0 0 2px #fff inset;
}
.dotnav-fall > li:after {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  visibility: hidden;
  content: '';
  background-color: rgba(0, 0, 0, 0.3);
  border-radius: 50%;
  opacity: 0;
  position: absolute;
  -webkit-transform: translate(0%, -200%);
  -ms-transform: translate(0%, -200%);
  -o-transform: translate(0%, -200%);
  transform: translate(0%, -200%);
  -webkit-transition: transform 0.3s ease 0s, opacity 0.3s ease 0s, visibility 0s ease 0.3s;
  -o-transition: transform 0.3s ease 0s, opacity 0.3s ease 0s, visibility 0s ease 0.3s;
  transition: transform 0.3s ease 0s, opacity 0.3s ease 0s, visibility 0s ease 0.3s;
}
.dotnav-fall > li > a {
  -webkit-transition: transform 0.3s ease 0s, opacity 0.3s ease 0s, background-color 0.3s ease 0s;
  -o-transition: transform 0.3s ease 0s, opacity 0.3s ease 0s, background-color 0.3s ease 0s;
  transition: transform 0.3s ease 0s, opacity 0.3s ease 0s, background-color 0.3s ease 0s;
}
.dotnav-fall > li.active:after {
  visibility: visible;
  opacity: 1;
  -webkit-transform: translate(0%, 0%);
  -ms-transform: translate(0%, 0%);
  -o-transform: translate(0%, 0%);
  transform: translate(0%, 0%);
  -webkit-transition: transform 0.3s ease 0s, opacity 0.3s ease 0s;
  -o-transition: transform 0.3s ease 0s, opacity 0.3s ease 0s;
  transition: transform 0.3s ease 0s, opacity 0.3s ease 0s;
}
.dotnav-fall > li.active > a {
