  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: inherit;
  height: inherit;
  cursor: pointer;
  border-radius: inherit;
  opacity: 0;
}
.color-selector > li input[type="radio"]:disabled {
  cursor: not-allowed;
}
.color-selector > li label {
  position: relative;
  font-family: "Web Icons";
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
}
.color-selector > li input[type="radio"]:checked + label:after {
  position: absolute;
  top: 0;
  left: 8px;
  display: inline-block;
  margin-top: -2px;
  font-size: 16px;
  line-height: 1;
  color: #ffffff;
  content: "\f192";
}
