.example-wrap {
  margin-bottom: 80px;
}
.example-wrap .example-wrap {
  margin-bottom: 0;
}
.example {
  margin-top: 20px;
  margin-bottom: 20px;
}
.example:before,
.example:after {
  content: " ";
  display: table;
}
.example:after {
  clear: both;
}
.example-title {
  text-transform: uppercase;
}
.example-title,
h4.example-title {
  font-size: 14px;
}
.panel-body > .example-wrap:last-child {
  margin-bottom: 0;
}
.panel-body > .row:last-child > [class*="col-"]:last-child .example-wrap:last-child {
  margin-bottom: 0;
}
.example-well {
  position: relative;
  margin-bottom: 30px;
  background-color: #f3f7f9;
}
.example-well .center {
  position: absolute;
  display: inline-block;
  top: 50%;
  left: 50%;
  max-width: 100%;
  max-height: 100%;
  -ms-transform: translate(-50%, -50%);
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
.example-dropdown .dropdown:before,
.example-dropdown .dropup:before,
.example-dropdown .dropdown:after,
.example-dropdown .dropup:after {
  content: " ";
  display: table;
}
.example-dropdown .dropdown:after,
.example-dropdown .dropup:after {
  clear: both;
}
.example-dropdown .dropdown > .dropdown-toggle,
.example-dropdown .dropup > .dropdown-toggle {
  float: left;
}
.example-dropdown .dropdown > .dropdown-menu,
.example-dropdown .dropup > .dropdown-menu {
  position: static;
  display: block;
  clear: left;
}
.example-dropdown .dropdown > .dropdown-menu-right,
.example-dropdown .dropup > .dropdown-menu-right {
  float: right;
  clear: right;
}
.example-tooltip {
  position: relative;
  display: inline-block;
  z-index: 1;
}
.example-tooltip .tooltip {
  position: relative;
  margin-right: 25px;
  opacity: 1;
}
.example-grid .example-col,
.example-blocks .example-col {
  min-height: 0;
  padding: 10px 15px 12px;
  background-color: #f3f7f9;
  border-radius: 0;
}
.example-grid .example-col {
  margin-bottom: 20px;
}
.example-grid .example-col .example-col {
  margin-top: 20px;
  margin-bottom: 0;
  background-color: #e2ecf1;
}
.example-popover {
  position: relative;
  display: inline-block;
  z-index: 1;
}
.example-popover .popover {
  position: relative;
  display: block;
  margin-right: 25px;
}
.example-buttons .btn,
.example-buttons .btn-group,
.example-buttons .btn-group-vertical {
  margin-right: 15px;
  margin-bottom: 20px;
}
.example-buttons .btn-group-vertical .btn,
.example-buttons .btn-group .btn {
  margin-right: 0;
  margin-bottom: 0;
}
.example-box {
  position: relative;
  margin-left: 0;
  margin-right: 0;
  padding: 45px 15px 15px;
  border: 1px solid #eeeeee;
}
.example-box:after {
  position: absolute;
  top: 15px;
  left: 15px;
  content: "Example";
  color: #959595;
  font-size: 12px;
  letter-spacing: 1px;
  text-transform: uppercase;
}
.example-avatars .avatar {
  margin-right: 20px;
  margin-bottom: 20px;
}
.example-avatars .avatar:last-child {
  margin-right: 20px;
}
.example-typography {
  position: relative;
  padding-left: 25%;
}
.example-typography .heading-note,
.example-typography .text-note {
  position: absolute;
  bottom: 2px;
  left: 0;
  display: block;
  width: 260px;
  font-size: 13px;
  font-weight: 400;
  line-height: 13px;
  color: #aab2bd;
}
.example-typography .text-note {
  top: 10px;
  bottom: auto;
}
.example-responsive {
  overflow-x: auto;
  min-height: 0.01%;
}
@media screen and (max-width: 767px) {
  .example-responsive {
    width: 100%;
    overflow-y: hidden;
    -ms-overflow-style: -ms-autohiding-scrollbar;
  }
}
